<template>
  <div class="mine">
    <div class="top"></div>
    <div class="mine-head">
      <div>
        <van-nav-bar title="我的" @click-right="setUp">
          <van-icon class="setting" name="setting-o" slot="right"/>
        </van-nav-bar>
      </div>
      <div class="clearfix user-information">
        <div class="userhead fl">
          <img class="auto-img" src="../../assets/images/head-portrait.jpeg" alt="">
        </div>
        <div class="user-box fl">
          <div class="user-name">{{userInfo.username}}</div>
          <div class="text">xxxx</div>
        </div>
      </div>
    </div>
    
    <div class="classification">
      <ul class="clearfix">
        <li class="">
          <div class="subject-icon"><span></span></div>
          <div class="subject">
            <span>{{mineData.ticket}}</span>
          </div>
        </li>
        <li class="">
          <div class="subject-icon"><span></span></div>
          <div class="subject">
            <span>{{mineData.payment}}</span>
          </div>
        </li>
        <li class="">
          <div class="subject-icon"><span></span></div>
          <div class="subject">
            <span>{{mineData.comment}}</span>
          </div>
        </li>
        <li class="">
          <div class="subject-icon"><span></span></div>
          <div class="subject">
            <span>{{mineData.member}}</span>
          </div>
        </li>
      </ul>
      <div class="watching-history clearfix">
        <div class="history-icon fl"><span></span></div>
        <div class="fl title">{{mineData.history}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="collection clearfix" @click="viewLikeMovie">
        <div class="collection-icon fl"><span></span></div>
        <div class="fl title">{{mineData.collection}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="wallet clearfix">
        <div class="wallet-icon fl"><span></span></div>
        <div class="fl title">{{mineData.wallet}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="follow clearfix">
        <div class="follow-icon fl"><span></span></div>
        <div class="fl title">{{mineData.concern}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="collection clearfix">
        <div class="collection-icon coupon fl"><span></span></div>
        <div class="fl title">{{mineData.coupon}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="collection clearfix">
        <div class="collection-icon feedback fl"><span></span></div>
        <div class="fl title">{{mineData.feedback}}</div>
        <div class="fr">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {createNamespacedHelpers} from 'vuex'
const {mapState} = createNamespacedHelpers('mineModule');

  export default {
    name: 'mine',
    
    computed: {
      ...mapState(['mineData', 'userInfo'])
    },

    methods: {
      setUp() {
        // console.log('进入设置面板');
        this.$router.push({name: 'setUp'});
      },

      // 查看我的收藏电影
      viewLikeMovie() {
        //验证是否登录
        let userStatus = localStorage.getItem('user');

        if (userStatus) {
          let userInfo = JSON.parse(userStatus);

          //如果登录
          if (userInfo.isLogin) {

            // console.log('查看我的收藏电影');

            this.$router.push({name: 'likeMovie'});

          } else {
            //如果未登录
            this.$toast('请先登录');
          }

        } else {
          //未登录
          this.$toast('请先登录');
        }
      }
    },
    created() {
      // 获取用户信息
      let userStatus = localStorage.getItem('user');

      if (userStatus) {
        let user = JSON.parse(userStatus);

        if (user.isLogin) {

          this.$store.commit('mineModule/changeUserInfo', {username: user.username});

        } else {
          this.$store.commit('mineModule/changeUserInfo', {username: ''});
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .mine {
    .top {
      background-color: #000;
      height: 24px;
    }
    .mine-head {
      background-color: #fff;
      
      .setting {
        font-size: 20px;
      }
      .van-nav-bar__title {
        font-size: 20px;
        font-weight: 700;
      }
      
      .user-information {
        padding: 10px;
      }
      .userhead {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
      }
      .user-box {
        width: calc(~"100% - 100px");
        margin: 10px;

        .text {
          height: 30px;
          line-height: 30px;
        }
      }
      .user-name {
        font-size: 18px;
        font-weight: 500;
        // margin-bottom: 8px;
        height: 30px;
        line-height: 30px;
      }
    }
    
    .classification {
      margin-top: 10px;
      padding: 0 10px;
      background-color: #fff;

      ul {
        li {
          float: left;
          width: 25%;
          margin: 10px 0;
           .subject-icon {
            span {
              margin: 0 auto;
              display: block;
              width: 36px;
              height: 36px;
            }
          }

          &:nth-child(1) {
            .subject-icon {
              span {
                background: url('../../assets/images/票.png') no-repeat center center;
                background-size: 32px 32px;
              }
            }
          }
          &:nth-child(2) {
            .subject-icon {
              span {
                background: url('../../assets/images/待付款.png') no-repeat center center;
                background-size: 30px 30px;
              }
            }
          }
          &:nth-child(3) {
            .subject-icon {
              span {
                background: url('../../assets/images/待评论.png') no-repeat center center;
                background-size: 28px 28px;
              }
            }
          }
          &:nth-child(4) {
            .subject-icon {
              span {
                background: url('../../assets/images/会员中心.png') no-repeat center center;
                background-size: 36px 36px;
              }
            }
          }

          .subject {
            text-align: center;
            font-size: 14px;
            margin-bottom: 4px;
          }
        }
      }

      .watching-history, .collection, .wallet, .follow {
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        padding: 6px 0;
        border-bottom: 1px solid #ddd;
      }
      .history-icon, .collection-icon, .wallet-icon, .follow-icon {
        span {
          display: block;
          width: 36px;
          height: 36px;
        }
      }
      .title {
        margin-left: 10px;
      }

      .history-icon {
        span {
          background: url('../../assets/images/历史.png') no-repeat center center;
          background-size: 24px 24px;
        }
      }

      .collection-icon {
        span {
          background: url('../../assets/images/我的收藏.png') no-repeat center center;
          background-size: 26px 26px;
        }
      }
      .wallet-icon {
        span {
          background: url('../../assets/images/钱包.png') no-repeat center center;
          background-size: 26px 26px;
        }
      }
      .follow-icon {
        span {
          background: url('../../assets/images/我的关注.png') no-repeat center center;
          background-size: 26px 26px;
        }
      }
      .coupon {
        span {
          background: url('../../assets/images/我的优惠卷.png') no-repeat center center;
          background-size: 32px 32px;
        }
      }
      .feedback {
        span {
          background: url('../../assets/images/意见反馈.png') no-repeat center center;
          background-size: 24px 24px;
        }
      }
    }
  }
</style>